import React, { useContext, useEffect } from "react";
import { StoreContext } from "../../store/StoreProvider";

const ShoppingCart = () => {
  const { cartStatus, setCartStatus } = useContext(StoreContext);

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  const prices = [];
  cartStatus.map((i) => prices.push(i.price));
  const pricesSum = prices.reduce((a, b) => a + b, 0);

  const cartItems = () => {
    if (cartStatus.length > 0) {
      const itemBodies = cartStatus.map((i) => {
        return (
          <div className="col-md-12">
            <div className="card mb-2 flex-row justify-content-center align-items-center">
              <img
                className="card-img-top p-1"
                style={{ width: "100px" }}
                src={`/images/${i.category}.jpg`}
                alt={i.title}
              />
              <div className="card-body">
                <p className="card-title mb-0">{i.title}</p>
                <p>{i.price.toFixed(2)} $</p>
                <button
                  data-value={i.id}
                  onClick={(e) => {
                    const itemId = e.target.getAttribute("data-value");
                    const itemIndex = cartStatus.findIndex(
                      (i) => i.id == itemId
                    );
                    let newList = [...cartStatus];
                    newList.splice(itemIndex, 1);
                    setCartStatus(newList);
                  }}
                >
                  Usuń z koszyka
                </button>
              </div>
            </div>
          </div>
        );
      });
      return itemBodies;
    } else {
      return (
        <div className="m-5">
          <p className="text-center">
            {" "}
            Brak produktów w koszyku. Przejdź do sklepu i wybierz coś dla
            siebie.
          </p>
        </div>
      );
    }
  };

  const summaryPanel = (
    <div className="col-md-12">
      <div className="card mb-2 flex-row justify-content-center align-items-center">
        <div className="card-body flex-row">
          <p className="card-title">
            łączna kwota{" "}
            <span>{(Math.round(pricesSum * 100) / 100).toFixed(2)}</span>{" "}
          </p>
          <button>Przejdź do dostawy</button>
        </div>
      </div>
    </div>
  );
  return (
    <div className="container" style={{ minHeight: "70vh" }}>
      <div className="my-3">
        <p className="font-weight-bold text-center text-uppercase">Koszyk</p>
      </div>
      {cartItems()}
      {cartStatus.length > 0 && summaryPanel}
    </div>
  );
};

export default ShoppingCart;
