import React, { useContext, useState } from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import { Link, NavLink } from "react-router-dom";
import { StoreContext } from "../store/StoreProvider";
import Search from "../components/Search/Search";
import $ from "jquery";

const MainNav = () => {
  const [bodyFixed, setBodyFixed] = useState(false);
  const { cartStatus, setCategory, isLoggedIn } = useContext(StoreContext);

  $(".navbar-collapse a").click(function () {
    $(".navbar-collapse").collapse("hide");
  });

  return (
    <nav class="navbar navbar-expand-lg sticky-top navbar-light bg-light">
      <div className="container fullsize">
        <Link class="navbar-brand" to="/">
          MainShop
        </Link>
        <button
          class="navbar-toggler"
          type="button"
          data-toggle="collapse"
          data-target="#navbarSupportedContent"
          aria-controls="navbarSupportedContent"
          aria-expanded="false"
          aria-label="Toggle navigation"
          onClick={() => {
            setBodyFixed(!bodyFixed);
            bodyFixed
              ? (document.body.style.height = "")
              : (document.body.style.height = "100%");
            bodyFixed
              ? (document.body.style.overflow = "")
              : (document.body.style.overflow = "hidden");
            // bodyFixed
            //   ? (document.querySelector(".fullsize").style.height = "")
            //   : (document.querySelector(".fullsize").style.height =
            //       "calc(100vh - 16px)");
          }}
        >
          <span class="navbar-toggler-icon"></span>
        </button>

        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav mr-auto">
            <li className="nav-item">
              <NavLink className="nav-link" to="/about">
                O nas
              </NavLink>
            </li>
            <li className="nav-item">
              <NavLink className="nav-link" to="/contact">
                Kontakt
              </NavLink>
            </li>
            <li className="nav-item dropdown">
              <NavLink
                class="nav-link dropdown-toggle"
                to="#"
                id="navbarDropdown"
                role="button"
                data-toggle="dropdown"
                aria-haspopup="true"
                aria-expanded="false"
              >
                Sklep
              </NavLink>
              <div
                class="dropdown-menu"
                aria-labelledby="navbarDropdown d-flex flex-column"
              >
                <Link
                  class="dropdown-item"
                  to="/shop"
                  onClick={() => {
                    setCategory("all");
                  }}
                >
                  Wszystkie produkty
                </Link>
                <div class="dropdown-divider"></div>
                <Link
                  class="dropdown-item"
                  to="/shop/mens-clothing/all"
                  onClick={() => {
                    setCategory(`men's clothing`);
                  }}
                >
                  Moda męska
                </Link>
                <Link
                  class="dropdown-item"
                  to="/shop/jewelery/all"
                  onClick={() => {
                    setCategory("jewelery");
                  }}
                >
                  Biżuteria
                </Link>
                <Link
                  class="dropdown-item"
                  to="/shop/electronics/all"
                  onClick={() => {
                    setCategory("electronics");
                  }}
                >
                  Elektronika
                </Link>
                <Link
                  class="dropdown-item"
                  to="/shop/womens-clothing/all"
                  onClick={() => {
                    setCategory(`women's clothing`);
                  }}
                >
                  Moda kobieca
                </Link>
              </div>
            </li>
          </ul>
          <Search />
          <ul class="navbar-nav">
            <li class="nav-item">
              <NavLink
                className="nav-link"
                to={isLoggedIn ? "/account" : "/welcome"}
              >
                {isLoggedIn ? "Twoje konto" : "Zaloguj"}
              </NavLink>
            </li>
            <li class="nav-item">
              <NavLink className="nav-link" to="/favourites">
                Ulubione
              </NavLink>
            </li>
            <li class="nav-item">
              <NavLink className="nav-link" to="/shopping-cart">
                Koszyk ({cartStatus.length})
              </NavLink>
            </li>
          </ul>
        </div>
      </div>
    </nav>
  );
};

export default MainNav;
