import React, { useContext, useEffect } from "react";
import { StoreContext } from "../../store/StoreProvider";
import ItemsShowList from "../Shop/subcomponents/ItemsShowList";
import { useLocation } from "react-router-dom";

const SearchView = () => {
  const { items } = useContext(StoreContext);
  const location = useLocation();

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  const searchedItem = location.state.searchedItem.search.toLowerCase();

  const itemsToShow = items.filter(
    (i) => !i.title.toLowerCase().indexOf(`${searchedItem}`)
  );

  return (
    <div className="container">
      <div className="row">
        <div className="col-md-3">panel </div>
        <ItemsShowList itemsArray={itemsToShow} searchedItems={itemsToShow} />
      </div>
    </div>
  );
};

export default SearchView;
