import React, { useContext, useEffect } from "react";
import { StoreContext } from "../../store/StoreProvider";

const Favourites = () => {
  const {
    favouritesList,
    setFavouritesList,
    items,
    cartStatus,
    setCartStatus,
  } = useContext(StoreContext);

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  const favouritesToShow = () => {
    return favouritesList.map((item) => {
      const isAlreadyInCart = cartStatus.filter((i) => {
        if (i.id == item.id) return i;
      });
      return (
        <div className="col-md-4">
          <div className="card mb-4">
            <p>{item.title}</p>
            <p>{item.price.toFixed(2)}</p>
            <button
              disabled={isAlreadyInCart.length > 0}
              data-value={item.id}
              onClick={(e) => {
                const itemId = e.target.getAttribute("data-value");
                const selectedItem = items.filter((i) =>
                  itemId == i.id ? i : null
                );

                if (isAlreadyInCart.length > 0) {
                  return;
                } else {
                  setCartStatus(cartStatus.concat(selectedItem));
                }
              }}
            >
              {/* <i class="fa fa-cart-plus"></i> */}
              {isAlreadyInCart.length > 0
                ? "Produkt znajduje się juz w koszyku"
                : "dodaj do koszyka"}
            </button>
            <button
              data-value={item.id}
              onClick={(e) => {
                const itemId = e.target.getAttribute("data-value");
                const itemIndex = favouritesList.findIndex(
                  (i) => i.id == itemId
                );
                let newList = [...favouritesList];
                newList.splice(itemIndex, 1);
                setFavouritesList(newList);
              }}
            >
              Usuń z ulubionych
            </button>
          </div>
        </div>
      );
    });
  };
  return (
    <div className="container" style={{ minHeight: "70vh" }}>
      <div className="my-3">
        <p className="font-weight-bold text-center text-uppercase">Ulubione</p>
      </div>
      {favouritesList.length <= 0 ? (
        <p className="text-center">Twoja lista życzeń jest pusta</p>
      ) : (
        <div className="row">{favouritesToShow()}</div>
      )}
    </div>
  );
};

export default Favourites;
