import React, { useContext, useState } from "react";
import { useHistory } from "react-router";
import { StoreContext } from "../../../store/StoreProvider";

const RegisterPanel = (
  setChangeAdress,
  handleNextStepClick,
  nameAndSurname = "",
  adress = "",
  postCodeCity = "",
  email = "",
  password = "",
  phone = ""
) => {
  const [userEmail, setUserEmail] = useState(email);
  const [userPassword, setUserPassword] = useState(password);
  const [userNameAndSurname, setUserNameAndSurname] = useState(nameAndSurname);
  const [userAdress, setUserAdress] = useState(adress);
  const [userPostcodeCity, setUserPostcodeCity] = useState(postCodeCity);
  const [userPhone, setUserPhone] = useState(phone);

  const {
    users,
    setUsers,
    setActiveUser,
    setIsLoggedIn,
    newUsersDb,
    setNewUsersDb,
  } = useContext(StoreContext);
  const history = useHistory();

  const handleEmailChange = (e) => {
    const data = e.target.value;
    setUserEmail(data);
  };
  const handlePasswordChange = (e) => {
    const data = e.target.value;
    setUserPassword(data);
  };
  const handleNameAndSurnameChange = (e) => {
    const data = e.target.value;
    setUserNameAndSurname(data);
  };
  const handleAdressChange = (e) => {
    const data = e.target.value;
    setUserAdress(data);
  };
  const handlePostcodeCityChange = (e) => {
    const data = e.target.value;
    setUserPostcodeCity(data);
  };
  const handlePhoneChange = (e) => {
    const data = e.target.value;
    setUserPhone(data);
  };

  const handleSubmit = (e) => {
    e.preventDefault();

    if (userEmail && userAdress && userPostcodeCity && userEmail && userPhone) {
      const newUser = {
        accessLevel: 0,
        nameAndSurname: userNameAndSurname,
        email: userEmail,
        phone: userPhone,
        adress: userAdress,
        postCodeCity: userPostcodeCity,
        items: [],
        login: "",
        password: userPassword,
        orders: [],
      };

      console.log(newUser);

      setNewUsersDb(newUsersDb.concat(newUser));
      // alternatywa przy problemie z fetchem users
      setUsers([...users, newUser]);

      setActiveUser(newUser);
      setIsLoggedIn(true);
      history.push("/account");
    } else {
      alert("uzupelnij wszystkie dane");
    }
  };

  return (
    <div className="container">
      <h3 className="text-center m-3">
        Zarejestruj się i zacznij korzystać z przywilejów już dzisiaj !
      </h3>
      <form className="row justify-content-around" onSubmit={handleSubmit}>
        <div className="col-md-5 bg-light m-2 p-4">
          <p className="text-center">Dane do logowania</p>
          <div class="form-group">
            <label for="exampleInputEmail1">Email</label>
            <input
              placeholder={email}
              onChange={handleEmailChange}
              type="text"
              value={userEmail}
              class="form-control"
              aria-describedby="emailHelp"
            />
            <small id="emailHelp" class="form-text text-muted">
              Email musi zawierać poprawny adres.
            </small>
          </div>
          <div class="form-group">
            <label>Hasło</label>
            <input
              placeholder={password}
              onChange={handlePasswordChange}
              type="password"
              value={userPassword}
              class="form-control"
            />
            <small id="emailHelp" class="form-text text-muted">
              Hasło musi się składać z min. 4 znaków
            </small>
          </div>
        </div>
        <div className="col-md-5 bg-light m-2 p-4">
          <p className="text-center">Dane adresowe</p>
          <div class="form-group">
            <label>Ulica i numer</label>
            <input
              placeholder={adress}
              onChange={handleAdressChange}
              type="text"
              value={userAdress}
              class="form-control"
              aria-describedby="emailHelp"
            />
            <small class="form-text text-muted">
              Proszę wpisać numer mieszkania poprzedzony literką m
            </small>
          </div>
          <div class="form-group">
            <label for="exampleInputPassword1">
              Kod pocztowy i miejscowość
            </label>
            <input
              placeholder={postCodeCity}
              onChange={handlePostcodeCityChange}
              type="text"
              value={userPostcodeCity}
              class="form-control"
            />
            <small id="emailHelp" class="form-text text-muted">
              Proszę podać kod pocztowy bez żadnych myślników.
            </small>
          </div>
        </div>
        <div className="col-md-5 bg-light m-2 p-4">
          <p className="text-center">Dane do kontaktu</p>
          <div class="form-group">
            <label for="exampleInputEmail1">Imię i nazwisko</label>
            <input
              placeholder={nameAndSurname}
              onChange={handleNameAndSurnameChange}
              type="text"
              value={userNameAndSurname}
              class="form-control"
              aria-describedby="emailHelp"
            />
            <small class="form-text text-muted">
              Dane potrzebne są dla kuriera.
            </small>
          </div>
          <div class="form-group">
            <label for="exampleInputPassword1">
              Numer telefonu komórkowego{" "}
            </label>
            <input
              placeholder={phone}
              onChange={handlePhoneChange}
              type="text"
              value={userPhone}
              class="form-control"
            />
            <small id="emailHelp" class="form-text text-muted">
              Numer porszę poprzedzić prefixem z danego kraju.
            </small>
          </div>
        </div>
        <div className="col-md-5 bg-light m-2 p-4 d-flex justify-content-center align-items-center flex-column">
          <h4 className="text-justify">
            Czy wiesz że jako zarejestrowany użytkownik możesz korzystać z
            naszego programu lojalnościowego ?
          </h4>
          <button
            type="button"
            style={{ display: "block" }}
            className="btn btn-success w-50 mx-auto mt-2"
          >
            Sprawdź
          </button>{" "}
        </div>
        <div className="col-md-3 d-flex justify-content-center">
          <button type="submit" className="btn btn-success mt-3 w-100">
            Zarejestruj
          </button>
        </div>
      </form>
    </div>
  );
};

export default RegisterPanel;
