import React, { useContext, useEffect, useState } from "react";
import { StoreContext } from "../../../store/StoreProvider";

const UserPanel = () => {
  const {
    users,
    newUsersDb,
    activeUser,
    setActiveUser,
    isLoggedIn,
    setIsLoggedIn,
  } = useContext(StoreContext);
  const [userName, setUserName] = useState("");

  useEffect(() => {
    setUserName(activeUser.nameAndSurname);
  }, []);

  return (
    <div>
      {" "}
      siema
      {activeUser && (
        <div className="container">
          {userName}
          {/* <p>Witaj {userName}</p> */}
        </div>
      )}
      <button
        onClick={() => {
          setIsLoggedIn(false);
          setActiveUser("");
        }}
      >
        wyloguj
      </button>
      <button
        onClick={() => {
          console.log(activeUser);
        }}
      >
        pokaz usera
      </button>
    </div>
  );
};

export default UserPanel;
