import React, { useContext, useState } from "react";
import { useHistory } from "react-router";
import { StoreContext } from "../../../store/StoreProvider";

const LoginPanel = () => {
  const { users, setActiveUser, setIsLoggedIn, newUsersDb } =
    useContext(StoreContext);
  const history = useHistory();

  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [validateMessage, setValidateMessage] = useState("");

  const handleOnChangeEmail = (event) => setEmail(event.target.value);
  const handleOnChangePassword = (event) => setPassword(event.target.value);

  const resetStateOfInputs = () => {
    setEmail("");
    setPassword("");
    setValidateMessage("");
  };

  const handleOnSubmit = (event) => {
    event.preventDefault();

    console.log(users);

    const userEmail = users.filter((user) =>
      user.email === email ? user.email : null
    );
    const userEmail2 = newUsersDb.filter((user) =>
      user.email === email ? user.email : null
    );
    const userPassword = users.filter((userPassword) =>
      userPassword.password === password ? userPassword : null
    );
    const userPassword2 = newUsersDb.filter((userPassword) =>
      userPassword.password === password ? userPassword : null
    );
    let activeUser = users.filter((user) =>
      user.email === email && user.password === password ? user : null
    );
    let activeUser2 = newUsersDb.filter((user) =>
      user.email === email && user.password === password ? user : null
    );
    console.log(userEmail, userPassword, activeUser, newUsersDb);
    if (
      (userEmail.length && userPassword.length && activeUser) ||
      (userEmail2.length && userPassword2.length && activeUser2)
    ) {
      setActiveUser(activeUser[0] || activeUser2[0]);
      setIsLoggedIn(true);
      resetStateOfInputs();
      history.push("/account");
    } else {
      setValidateMessage("Niepoprawny adres email lub hasło. spróbuj ponownie");
    }
  };

  const validateMessageInfo = validateMessage.length ? (
    <small id="emailHelp" class="form-text text-center text-danger">
      {validateMessage}
    </small>
  ) : null;

  return (
    <div className="col-md-5 bg-light m-2 p-4">
      <form className=" m-auto" onSubmit={handleOnSubmit}>
        <div class="form-group">
          <label>Email</label>
          <input
            onChange={handleOnChangeEmail}
            value={email}
            type="email"
            class="form-control"
            aria-describedby="emailHelp"
            placeholder="Wpisz swój adres email"
          />
          <small id="emailHelp" class="form-text text-muted">
            Twoje dane są u nas bezpieczne.
          </small>
        </div>
        <div class="form-group mb-5">
          <label>Hasło</label>
          <input
            onChange={handleOnChangePassword}
            type="password"
            value={password}
            class="form-control"
            placeholder="Hasło"
          />
          {validateMessageInfo}
        </div>
        <button
          type="submit"
          style={{ display: "block" }}
          className="btn btn-primary w-50 m-auto"
        >
          Zaloguj się
        </button>
      </form>
    </div>
  );
};

export default LoginPanel;
