import React from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import { useHistory } from "react-router-dom";

const Header = () => {
  let history = useHistory();
  return (
    <div className="jumbotron jumbotron-fluid">
      <div className="container">
        <div className="jumbotron-bg"></div>
        <h1 className="display-4">Witamy w MainShop</h1>
        <p className="lead">Znajdziesz tu wszystko, czego potrzebujesz ...</p>
        <button
          className="btn btn-lg btn-primary"
          onClick={() => {
            // console.log(
            //   document.getElementById("okruszki").getBoundingClientRect().top
            // );
            // document.getElementById("okruszki").scrollIntoView();
            // console.log("na pewno ?");
            history.push("/shop");
          }}
        >
          Sprawdź oferte !
        </button>
      </div>
    </div>
  );
};

export default Header;
