import React, { useState } from "react";

import { Prompt } from "react-router";

const Mail = () => {
  const [note, setNote] = useState("");
  const [email, setEmail] = useState("");
  const [name, setName] = useState("");
  const [isEmpty, setIsEmpty] = useState(true);
  const [isSent, setIsSent] = useState(false);

  const handleNameChange = (e) => {
    const userName = e.target.value;
    setName(userName);
  };

  const handleMailChange = (e) => {
    const userMail = e.target.value;
    setEmail(userMail);
  };

  const handleNoteChange = (e) => {
    const newNote = e.target.value;
    setNote(newNote);
    if (e.target.value) {
      setIsEmpty(false);
    } else {
      setIsEmpty(true);
    }
    console.log(isEmpty);
  };

  const handleSubmit = (e) => {
    e.preventDefault();

    if (note && email && name) {
      const message = { name, email, note };
      // usersMessages.push(message)

      setName("");
      setEmail("");
      setNote("");
      setIsSent(true);
      setTimeout(() => {
        setIsSent(false);
      }, 4000);
    } else {
      alert("wypelnij wszystkie pola");
    }
  };

  const sentMessage = <div>Wiadomość wysłana!</div>;

  return (
    <div className="col-md-6 bg-light p-3">
      <div className="container ">
        <p className="text-center">Napisz do nas !</p>
        <form
          className="d-flex justify-content-center flex-column"
          onSubmit={handleSubmit}
        >
          <div class="form-group mx-5">
            <label for="formGroupExampleInput">Imię</label>
            <input
              value={name}
              onChange={handleNameChange}
              type="text"
              class="form-control"
              placeholder="Wpisz swoje imię"
            />
          </div>
          <div class="form-group mx-5">
            <label for="formGroupExampleInput2">Email</label>
            <input
              value={email}
              onChange={handleMailChange}
              type="email"
              class="form-control"
              placeholder="wpisz swój adres email"
            />
          </div>
          <div class="form-group mx-5">
            <textarea
              value={note}
              onChange={handleNoteChange}
              rows="5"
              cols="33"
              type="text"
              class="form-control"
              placeholder="Wpisz treść wiadomości"
            />
          </div>
          <button className="mx-5" type="submit">
            Wyślij
          </button>
        </form>
        <Prompt
          when={!isEmpty}
          message="Masz wpisaną wiadomość w formularzu? czy na pewno chcesz opuścic stronę ?"
        />
        {isSent && sentMessage}
      </div>
    </div>
  );
};

export default Mail;
