import React, { createContext, useEffect, useState } from "react";
import usersList from "./users.json";

export const StoreContext = createContext(null);

const StoreProvider = ({ children }) => {
  const [items, setItems] = useState();
  const [users, setUsers] = useState();
  const [activeUser, setActiveUser] = useState("");
  const [category, setCategory] = useState("all");
  const [cartStatus, setCartStatus] = useState([]);
  const [favouritesList, setFavouritesList] = useState([]);
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [newUsersDb, setNewUsersDb] = useState([]);

  // useEffect(() => {
  //   fetch("./db/items.json")
  //     .then((res) => res.json())
  //     .then((data) => setItems(data.itemsData))
  //     .catch((err) =>
  //       console.log("błąd przy pobieraniu danych o produktach", err)
  //     );
  // }, []);

  // problem z fetchem na ghpages- alternatywne rozwiazanie na dole

  // useEffect(() => {
  //   fetch("./db/users.json")
  //     .then((res) => res.json())
  //     .then((data) => setUsers(data.usersData))
  //     .catch((err) =>
  //       console.log("błąd przy pobieraniu danych o użytkownikach", err)
  //     );
  // }, []);

  useEffect(() => {
    const listOfUsers = usersList.usersData;
    setUsers(listOfUsers);
  }, []);

  useEffect(() => {
    fetch("https://fakestoreapi.com/products?limit=20")
      .then((res) => res.json())
      .then((json) => setItems(json))
      .catch((err) => console.log(err));
  }, []);

  return (
    <StoreContext.Provider
      value={{
        items,
        setItems,
        users,
        setUsers,
        cartStatus,
        setCartStatus,
        favouritesList,
        setFavouritesList,
        category,
        setCategory,
        isLoggedIn,
        setIsLoggedIn,
        activeUser,
        setActiveUser,
        newUsersDb,
        setNewUsersDb,
      }}
    >
      {children}
    </StoreContext.Provider>
  );
};

export default StoreProvider;
